.addStudentForm {
  min-width: 300px;
  max-width: 100%;
  
  margin: auto;
  padding: 10px;
  border-radius: 7px;
  margin: 10px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.form-control {
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
  max-width: 300px;
}
.form-control label {
  text-align: left;
  display: block;
  margin-bottom: 10px;
  color: rgb(0, 0, 0);
}
.form-control input {
  padding: 8px 8px 8px 5px;
  width: 100%;
  outline: none;
  font-size: 15px;
  border-radius: 7px;
  border: none;
  /* padding-left: 2px; */
}
.form-control select {
  padding: 8px 8px 8px 5px;
  width: 100%;
  outline: none;
  font-size: 15px;
  border-radius: 7px;
  border: none;
}
