    flex: 0 0 30%;
  }
  
  .form-control input[type="text"],
  .form-control input[type="number"] {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .btn {
    display: block;
    width: 100%;
