.import-data {
  margin: 20px auto;
  width: fit-content;
  border-radius: 10px;
  background-color: #b4433b;
  padding: 5px 10px;
}
.import-data form {
  display: flex;
  flex-direction: column;
}
.import-data form input {
  margin: 20px auto;
  color: white;
}

.import-data h2 {
  color: white;
}

.import-data form button {
  cursor: pointer;
  text-transform: uppercase;

  padding: 10px;
  border: none;
  border-radius: 10px;
  font-weight: bolder;
}


.fees-accordion-box{
  display: flex;
  justify-content: center;
  margin: 1rem;
  flex-direction: column;
  gap: 2rem;
}

.heading{
  background-color: #c0c0c05c;
  text-align: left;
  padding: 0.5rem 1rem;
}